body {
    top: 0;
    margin: 0;
    padding: 0;
    max-height: 100vh;
    overflow: hidden;
    box-sizing: border-box;
}

.main {
    box-sizing: border-box;
    // height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 30px;
    // height: 100vh;

    .title {
        box-sizing: border-box;
        margin: 0;
        margin-bottom: 5px;
        // height: calc(var(--vh, 1vh) * 10);
        height: 10vh;
        $stroke-width: 35px * 0.2;
        text-transform: uppercase;
        font-size: 40px;
        color: $darkColor;
        text-align: center;
    }

    .form-login {
        display: flex;
        flex-direction: column;
        align-items: center;
        box-sizing: border-box;
        background-color: white;
        border-radius: 5px;
        border: 3px solid $deathColor;
        margin-top: 20px;
        width: 300px;
        height: 270px;
        padding: 20px 40px;

        .label {
            display: block;
            width: 100%;
            margin-bottom: 10px;
            font-family: Courier;
            font-weight: bold;
            color: $darkColor;
            font-size: 20px;
        }

        .form--input-text {
            padding: 10px;
            display: block;
            width: 190px;
            border: 1px solid $deathColor;
            transition: ease 0.3s, border 0.3s;
            color: $darkColor;
            margin-bottom: 15px;
            font-size: 1.2em;
            border-radius: 5px;

            &:focus {
                border: 1px solid $secundaryColor;
            }
        }

        .form--input-submit {
            cursor: pointer;
            margin: 0 auto;
            width: 190px;
            height: 35px;
            background-color: $darkColor;
            border: 1px solid $darkColor;
            border-radius: 5px;
            color: white;
            transition: ease 0.3s;

            &:hover {
                background-color: #01627a;
            }
        }
    }

    .chat {
        background-color: white;
        border-radius: 5px;
        border: 3px solid $deathColor;
        box-sizing: border-box;
        width: 100vw;
        height: 85vh;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-around;

        .chat-idRoom {
            width: 100%;
            font-weight: bold;
            padding-bottom: 20px;
            color: $darkColor;
            text-align: center;
            font-family: Courier;
            font-size: 2em;
            border-bottom: 2px solid $deathColor;
            margin-bottom: 0;
        }

        .chat-number-users {
            cursor: pointer;
            font-weight: bold;
            color: $darkColor;
            text-align: center;
            .chat-number-users-text {
                font-family: Courier;
                font-size: 1.4em;
                padding: 0;
            }
        }

        .chat-msgs {
            box-sizing: border-box;
            height: 70%;
            width: 100%;
            overflow-y: scroll;
            border-top: 2px solid $deathColor;
            flex: 1;
            .chat-msg {
                font-family: Courier;
            }
            .chat-msg--system {
                text-align: center;
            }
            .chat-msg--other {
                color: $deathColor;
            }
            .chat-msg--user {
                text-align: right;
                color: $secundaryColor;
            }
        }

        .form-message {
            box-sizing: border-box;
            padding-left: 5px;
            padding-right: 5px;
            width: 100%;
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 10px;
            .chat-input--message {
                // flex-grow: 3;
                // flex-shrink: 3;
                width: 100%;
                padding: 10px;
                display: inline-block;
                border: 1px solid $deathColor;
                transition: ease 0.3s, border 0.3s;
                color: $darkColor;
                font-size: 1.2em;
                border-radius: 5px;
                margin-right: 5px;

                &:focus {
                    border: 1px solid $secundaryColor;
                }
            }
            .chat-input--submit {
                right: 5px;
                padding: 10px;
                border: 1px solid $deathColor;
                font-size: 1.2em;
                border-radius: 5px;
                cursor: pointer;
                background-color: $darkColor;
                color: white;
            }
        }
    }

    .chat-users {
        font-family: Courier;
        background-color: #ddd;
        position: absolute;
        box-sizing: border-box;
        border-radius: 5px;
        margin-top: 70px;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        border: 3px solid $deathColor;
        height: 50%;
        width: 50%;
        padding: 10px 30px;
        overflow-y: scroll;

        .fa-times {
            cursor: pointer;
            position: absolute;
            right: 20px;
            top: 20px;
        }

        .chat-users--title {
            font-size: 1.5em;
            font-weight: bold;
            color: $darkColor;
        }

        .chat-users--list {
            .chat-users--user {
                font-size: 1.3;
                font-weight: bold;
                color: $deathColor;
            }
        }
    }
}
